@import 'https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,300;0,400;0,500;0,700;0,800;0,900;1,100;1,300;1,400;1,500;1,700;1,800;1,900&display=swap';

body {
  margin: 0;
  padding: 0;
  font-family: "Poppins", sans-serif !important;
}

table{
  border-collapse: collapse;
  font-size: 10px;
}
table tr, table td, table th{
  border: 2px solid #fff5;
  padding: 8px 10px;
  text-align: start;
}